import React from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";

const Home = () => {
  const { locale } = useSelector(state => ({
    locale: state.app.locale,
  }));

  return (
    <>
      <p>Welcome to Razzle.</p>

      <h1>Locale: {locale}</h1>

      <Link to="about">About</Link>
    </>
  );
};

export default Home;
